<template>
<div class="hello">
  <div class="content">
    <h1 class="title">PUREHEALTH . FITNESS</h1>
  </div>
  <div class="fullscreen-overlay">
  </div>
  <video id="bgvid" muted autoplay loop>
   <source :src="videos.mp4" type="video/mp4" />
   <source :src="videos.webm" type="video/webm" />
 </video>
</div>
</template>

<script>
import aerobics_mp4 from '../assets/aerobics.mp4';
import aerobics_webm from '../assets/aerobics.webm';
export default {
  name: 'hello',
  data() {
    return {
      videos: {
        mp4: aerobics_mp4,
        webm: aerobics_webm
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.fullscreen-overlay {
  background-color: rgba(255, 255, 255, 0.3);
  z-index: -99;
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

video#bgvid {
  max-width: none;
  position: fixed;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  transform: translateX(-50%) translateY(-50%);
}
</style>



