import Vue from 'vue'
import agent from '../agent';

function middleware(user) {
  window.localStorage.setItem('jwt', user.token);
  agent.setToken(user.token);
}

export default {
  getUser: ({ commit }, payload) => {
    return agent.Auth.current()
      .then(res => {
        middleware(res.user);
        commit('SET_USER', res.user);
      })
      .catch(err => console.log(err));
  },
  register: ({ commit }, payload) => {
    return agent.Auth.register(payload.username, payload.email, payload.password)
      .then(res => {
        middleware(res.user);
        commit('SET_USER', res.user);
      })
      .catch(err => console.log(err));
  },
  login: ({ commit }, payload) => {
    return agent.Auth.login(payload.email, payload.password)
      .then(res => {
        middleware(res.user);
        commit('SET_USER', res.user)
      })
      .catch(err => console.log(err));
  },
  logout: ({ commit }) => {
    window.localStorage.setItem('jwt', '');
    agent.setToken(null);
    commit('SET_USER', {});
  }
}



