<template>
<div class="account">
  <div class="section">
    <div class="content">
      <h1>{{cartTotal}}</h1>
      <div v-if="cart" v-for="product in cart">
        {{product}}
      </div>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'Account',
  props: ['cart'],
  data() {
    return {}
  },
  computed: {
    cartTotal() {
      if (this.cart && this.cart.length) {
        return `$${(this.cart.reduce((acc, curr) => acc + parseFloat(curr.cost), 0)).toFixed(2)}`;
      } else {
        return '$0.00';
      }
    }
  }
}
</script>

