<template>
<div class="shop content">
  <div class="section product-list">
    <div class="container">
      <div class="filter-container">
        <button @click="sortByPrice">
          <icon v-if="sort.price.directionHighLow" name="chevron-down"></icon>
          <icon v-else name="chevron-up"></icon>
          Sort By Price
        </button>
      </div>
      <div class="product-grid">
        <div class="product-grid-item" v-for="product in products" v-if="product.active">
          <div class="product-img image">
            <img :src="product.image" />
          </div>
          <div class="product-description">
            <div class="product-name">
              <h2>{{product.name}}</h2>
            </div>
            <p>{{product.description}}</p>
            <div class="product-colors">
              <details>
                <summary>
                  Available Colors
                </summary>
                <small><span v-for="color in product.colors"><em>{{color}}</em></span></small>
              </details>
            </div>
            <div class="product-cost">
              <b>${{product.cost}}</b>
            </div>
            <div class="product-add-button">
              <button class="button-primary" @click="$emit('addToCart', product)">Add To Cart</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>
<style scoped>
.filter-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 1rem;
}
.fa-icon {
  width: auto;
  height: 11px;
  margin-right: 4px;
}

.product-list {
  padding: 0 1rem;
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-auto-rows: 2rem 1fr repeat(3, auto);
  grid-gap: 2rem;
}

@media(min-width: 769px) {
  .product-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media(min-width: 900px) {
  .product-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media(min-width: 1024px) {
  .product-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media(min-width: 1400px) {
  .product-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.product-grid-item {
  background-color: #e8e8e8;
  text-align: center;
  padding-bottom: 1rem;
  position: relative;
}

.product-grid-item>div {
  margin-bottom: 0.5rem;
}

.product-name h2 {
  font-size: 1.35rem;
}

.product-cost {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 0.5rem;
  font-size: 1.5rem;
}

.product-description p {
  padding: 0 1rem;
}
.product-add-button {
  position: absolute;
  bottom: 0;
  right: 0;
  padding: 0.5rem;
}
.product-add-button button {
  margin-top: 0.5rem;
  font-size: 0.75rem;
  padding: 0.5rem;
  height: 1.77rem;
  line-height: 0.5rem;
  letter-spacing: 0.03em;
}

.product-colors {
  position: relative;
  height: 5rem;
}

details {
  position: absolute;
  top: 0;
  left: 50%;
  width: 200px;
  height: 100px;
  margin-left: -100px;
  cursor: pointer;
}

.colors {
  font-size: 0.7rem;
}

.colors span {
  display: inline-block;
  margin-right: 4px;
  border: 0.5px solid transparent;
  border-radius: 12px;
  padding: 1px 4px;
}

.colors span:hover {
  border: 0.5px solid black;
}

.colors span.active {
  border: 1px solid black;
}

th,
td {
  text-align: center;
}

table {
  margin: 0 auto;
}

table {
  width: 100%;
}

table td,
table th {
  text-align: left;
  vertical-align: top;
}

table th {
  color: #363636;
}

.table {
  background-color: white;
  color: #363636;
  margin-bottom: 1.5rem;
  width: 100%;
}

.table td,
.table th {
  border: 1px solid #dbdbdb;
  border-width: 0 0 1px;
  padding: 0.5em 0.75em;
  vertical-align: top;
}

.table th {
  color: #363636;
  text-align: left;
  cursor: pointer;
}

.table tr:hover {
  background-color: #fafafa;
}

.table thead td,
.table thead th {
  border-width: 0 0 2px;
  color: #7a7a7a;
}

.table tfoot td,
.table tfoot th {
  border-width: 2px 0 0;
  color: #7a7a7a;
}

.table tbody tr:last-child td,
.table tbody tr:last-child th {
  border-bottom-width: 0;
}

table td img {
  width: 128px;
  height: auto
}

table td:last-child button {
  padding: 0 15px;
  height: 30px;
  line-height: 30px;
  display: block;
  margin: 0 auto;
}

table td:last-child button:hover {
  color: #333;
  border-color: #888;
  outline: 0;
}

details:not([open]) {
  margin-bottom: 15px;
}

details,
summary {
  user-select: none;
  outline: none;
  cursor: pointer;
}
</style>
<script>
import faker from 'faker';

function setProducts() {
  let products = [];
  for (let i = 0; i < 10; i++) {
    let product = {
      id: faker.random.uuid(),
      name: faker.commerce.productName(),
      description: faker.lorem.sentence(),
      image: require(`../assets/image0${i}.jpeg`),
      cost: faker.commerce.price(),
      colors: [],
      active: true
    }
    for (let j = 0; j < 4; j++) {
      product.colors.push(getColor(product.colors, j));
    }
    products.push(product);
  }

  function getColor(colors, j) {
    let color = faker.commerce.color()
      .split(' ')
      .map(c => c.charAt(0).toUpperCase() + c.slice(1));
    if (colors.includes(color)) return getColor(colors, j);
    let delimiter = j >= 3 ? '' : ', ';
    return color.join(' ') + delimiter;
  }
  return products;
}
const appProducts = setProducts();

function getColorList(products) {
  let colors = [];
  for (let i = 0; i < products.length; i++) {
    for (let j = 0; j < products[i].colors.length; j++) {
      if (!colors.includes(products[i].colors[j])) {
        colors.push(products[i].colors[j]);
      }
    }
  }
  return colors.map(color => {
    let comma_index = color.split('').indexOf(',');
    if (comma_index >= 0) {
      return {
        color: color.substr(0, comma_index),
        active: false
      };
    } else {
      return {
        color,
        active: false
      };
    }
  });
}
const colorList = getColorList(appProducts);
export default {
  name: 'Shop',
  data() {
    return {
      products: appProducts,
      colorList: colorList,
      sort: {
        price: {
          directionHighLow: false
        }
      }
    }
  },
  created() {
    this.sortByPrice();
  },
  methods: {
    toggleActive(e) {
      let colorIndex = this.colorList.indexOf(this.colorList.find(color => color.color === e.target.textContent));
      let newColor = Object.assign({}, this.colorList[colorIndex]);
      if (e.target.classList.contains('active')) {
        e.target.classList.remove('active');
        newColor.active = false;
      } else {
        e.target.classList.add('active');
        newColor.active = true;
      }
      this.colorList.splice(colorIndex, 1, newColor);
      this.filterByColor();
    },
    sortByPrice() {
      this.products = this.products.sort((a, b) => {
        if (this.sort.price.directionHighLow) {
          return a.cost - b.cost;
        } else {
          return b.cost - a.cost;
        }
      });
      this.sort.price.directionHighLow = !this.sort.price.directionHighLow;
    },
    filterByColor() {
      let active = this.colorList.filter(item => item.active);
      this.products.forEach(product => {
        product.active = true;
        let colors = product.colors.map(color => this.removeComma(color));
        active.forEach(activeItem => {
          if (!colors.includes(activeItem.color)) {
            product.active = false;
          }
        });
      });
    },
    removeComma(color) {
      let comma_index = color.split('').indexOf(',');
      if (comma_index >= 0) {
        return color.substr(0, comma_index);
      } else {
        return color;
      }
    }
  }
}
</script>



