<template>
<div class="login">
  <div class="section">
    <div class="container">
      <div class="field">
        <label class="label">Username</label>
        <p class="control">
          <input class="input" type="email" placeholder="Username" v-model="username">
        </p>
      </div>
      <div class="field">
        <label class="label">Email</label>
        <p class="control">
          <input class="input" type="email" placeholder="Email Address" v-model="email">
        </p>
      </div>
      <div class="field">
        <label class="label">Password</label>
        <p class="control">
          <input class="input" type="password" placeholder="Password" v-model="password">
        </p>
      </div>
      <div class="field">
        <p class="control">
          <input class="button button-primary" type="submit" value="Submit" @click="submitRegister" />
        </p>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import {
  mapActions
} from 'vuex';

export default {
  name: 'Login',
  data() {
    return {
      username: '',
      email: '',
      password: ''
    }
  },
  methods: {
    submitRegister() {
      this.register({
          username: this.username,
          email: this.email,
          password: this.password
        })
        .then(() => {
          this.$router.push({
            path: '/account'
          });
        })
        .catch(err => console.log(err));
    },
    ...mapActions(['register'])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login {
  padding: 2rem;
}

.container {
  margin: 0 auto;
  max-width: 40%;
}
</style>



